.yoga {
  display: flex;
  flex-wrap: wrap;
}

.yoga-image img {
  width: 10%;
  margin-top: 60px;
}

.yoga-des {
  margin-left: 80px;
  flex: 1;
}

.yoga-des h1 {
  font-weight: 500;
  color: #000000;
  letter-spacing: 0.5px;
  line-height: 1.2;
  text-transform: capitalize;
  font-family: 'Georgia', serif;
  font-size: 35px;
}

.yoga-des p {
  font-family: 'Georgia', serif;
  font-weight: 400;
  font-style: normal;
  line-height: 1.8;
  letter-spacing: 0.5px;
}

.origin p {
  margin-left: 50px;
  font-family: 'Georgia', serif;
  font-weight: 400;
  font-style: normal;
  line-height: 1.8;
  letter-spacing: 0.5px;
}

.click {
  margin-left: 20px;
}

.click h2 {
  font-weight: 500;
  color: #2c0202;
  letter-spacing: 0.5px;
  line-height: 1.2;
  text-transform: capitalize;
  font-family: 'Georgia', serif;
  font-size: 25px;
}

.button {
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-left: 720px;
  border: 2px solid;
  border-radius: 10px;
  z-index: 1;
  transition: 5s;
  overflow: hidden;
  background-color: rgba(224, 171, 171, 0.749);
}

.button:hover {
  background-color: transparent;
  color: rgb(0, 0, 0);
  transition: background-color 0.3s ease, color 0.3s ease;
}

@media screen and (min-width: 769px) {
  .yoga-image img {
    width: 400px;
    height: 400px;
    margin-left: 50px;
    padding-top: 25px;
  }

  .yoga-des {
    margin-left: 80px;
  }

  .button {
    padding: 15px 30px;
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 768px) {
  .yoga {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .yoga-image img {
    width: 70%;
    height: auto;
    margin: 0;
    padding-top: 10px;
  }

  .yoga-des {
    margin-left: 20px;
    text-align: center;
  }

  .origin {
    margin-left: 20px;
    margin-right: 20px;
  }

  .click {
    margin-left: 0;
    margin-right: 0;
    text-align: center;
    font-size: 8px;
    margin-top: 35px;
  }

  .button {
    margin-left: 40%;
    margin-top: 20px;
    padding: 12px 24px;
    font-size: medium;
    margin-bottom: 20px;
  }
}
